import { useState } from 'react';
import { styled } from 'styled-components';

const WrapperParagraph = styled.p`
  color: blue;
  background-color: green;
`;

const StyledParagraph = ({ text }: { text: string }) => (
  <WrapperParagraph>{text}</WrapperParagraph>
);

const RedPharagraph = styled(StyledParagraph)`
  background: red;
`;

const HOCParagraph = (Component: React.FC<{ text: string }>) => {
  return (props: { text: string }) => <Component {...props} />;
};
const RedPharagraphHOC = HOCParagraph(RedPharagraph);

export const Playground: React.FC = () => {
  const [value, setValue] = useState('');
  return (
    <div>
      <h1>Playground</h1>
      <input
        type='text'
        value={value}
        onChange={(e) => setValue(e.target.value)}
        id='text123'
      />
      <textarea autoFocus>
        At w3schools.com you will learn how to make a website. We offer free
        tutorials in all web development technologies.
      </textarea>

      <RedPharagraphHOC text='This is a red paragraph' />
      <StyledParagraph
        text={
          'This is a playground page. You can use it to test new features and components.'
        }
      />
    </div>
  );
};
